.page {
  font-family: Cinzel, Roboto, serif;
  color: $main-dark;
  size: 16px;
  line-height: 27px;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      pointer-events: all;
      transform: translateX(0);
    }
  }
}

p {
  margin: 0;
  padding: 0;
}
