.exhibition {
  &__images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 24px;
    margin: 48px 0 24px;

    @include on-tablet {
      grid-template-columns: repeat(6, 80px);
      gap: 24px;
    }

    @include on-desktop {
      grid-template-columns: repeat(12, 63px);
      gap: 24px;
    }

    // &--two {
    //   grid-template-columns: repeat(2, 130px);
    //   column-gap: 20px;

    //   @include on-tablet {
    //     grid-template-columns: repeat(6, 63px);
    //   }
    // }

  //   &--three {
  //     grid-template-columns: repeat(3, 88px);
  //     column-gap: 8px;
  //   }
  }

  &__image {
    width: 100%;
    height: 320px;
    object-fit: cover;

    &--medium {
      // width: 130px;
      // height: 130px;
      // grid-column: span 4;
    }

    &--small {
      // width: 88px;
      // height: 87px;
      grid-column: span 4;
    }

    &--large {
      // width: 280px;
      // height: 277px;
      grid-column: span 8;
    }

  }

  &__title {
    margin: 0;
    color: $accent-main;
    font-size: 16px;
    line-height: 27px;
    font-weight: 700;

    &--m {
      font-size: 24px;
      line-height: 33.6px;
      text-align: center;
    }
  }

  &__dates {
    font-size: 16px;
    line-height: 27px;
    font-weight: 400;
    color: $main-dark
  }

  &__description {
    font-family: Roboto, sans-serif;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
  }
}
